@import "colors";

@font-face {
    font-family: "Black";
    src: url("./MADE TOMMY Black_PERSONAL USE.otf") format("opentype");
}

@font-face {
    font-family: "Bold";
    src: url("./MADE TOMMY Bold_PERSONAL USE.otf") format("opentype");
}

@font-face {
    font-family: "Regular";
    src: url("./MADE TOMMY Regular_PERSONAL USE.otf") format("opentype");
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, button, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, input, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: inherit;
    font-family: Regular;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
    display: block;
}

html {
    scroll-behavior: smooth;
}

body {
    line-height: 1;
    font-size: 16px;
    background-color: $background;
}

ol, ul {
    list-style: none;
}

a {
    color: $blue;
    text-decoration: none;
}

p {
    color: $black;
}

blockquote, q {
    quotes: none;
}

blockquote::before, blockquote::after, q::before, q::after {
    content: "";
    content: none;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

textarea {
    resize: none;
}

@media only screen and (max-width: 48rem) {
    body {
        font-size: 14px;
    }
}