@import "colors";

#main-menu {
    position: fixed;
    z-index: 3;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: .5em;
    button {
        background: $blue;
        color: $white;
        padding: 1em 4em;
        width: 15em;
        border: .4em solid $blue;
        border-radius: 1em;
        font-size: 1em;
        transition: .2s ease;
        cursor: pointer;
        &:hover {
            background: $background !important;
            color: $blue;
        }
    }
    a button {
        background: $gray;
        border-color: $gray;
        &:hover {
            color: $gray;
        }
    }
    a.kevydev-button button {
        background: $green;
        border-color: $green;
        &:hover {
            color: $green;
        }
    }
}