@import "colors";

#game {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    .table {
        display: grid;
        grid-template-columns: repeat(3, 6em);
        grid-template-rows: repeat(3, 6em);
        gap: .5em;
        .element-in-table {
            background: $white;
            width: 100%;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            cursor: pointer;
            border-radius: 1em;
            &.winner {
                animation: animated-winner 4s ease;
            }
            div {
                font-size: 3em;
                font-family: Bold;
                &.X-selected {
                    color: $cruz;
                }
                &.O-selected {
                    color: $cerito;
                }
                &.shadow {
                    color: $gray;
                }
            }
        }
    }
}

@keyframes animated-winner {
    20% {
        background: $green;
    }
    40% {
        background: $white;
    }
    60% {
        background: $green;
    }
    80% {
        background: $white;
    }
    100% {
        background: $green;
    }
}