@import "colors";

header {
    position: fixed;
    z-index: 4;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 5.75em;
    padding: 1em;
    background: $white;
    border-bottom: 0.125em solid $shadow;
    display: flex;
    align-items: center;
    justify-content: center;
    .logotype {
        font-size: 3em !important;
        .animated-o {
            color: $cerito;
            animation: infinite ease animated-o 2s;
        }
        .animated-x {
            color: $cruz;
            animation: infinite ease animated-x 2s;
        }
    }
}

@keyframes animated-o {
    0% {
        font-size: 2rem;
    }
    50% {
        font-size: 3rem;
    }
    100% {
        font-size: 2rem;
    }
}

@keyframes animated-x {
    0% {
        font-size: 3rem;
    }
    50% {
        font-size: 2rem;
    }
    100% {
        font-size: 3rem;
    }
}